ul{
    list-style-type: none;
    background-color: #4CAF50;
    user-select: none;
    
    font-size: 20px;
}
#nestList{
    position: relative;
    display: inline-block;

}
#dropdown-content{
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;

}

#nestList:hover #dropdown-content{
    display: block;
}